.map {
  width: 100%;
  height: 100%;
}
.mapwrapper {
  position: absolute;
  left: 0;
  top: 0;
  height: 100vh;
  width: calc(100% - 400px);
  float: left;
}
.mapwrapper.sticky {
  position: fixed;
}
.mapwrapper.bottom {
  bottom: 0;
  top: auto;
}
.map-overlay {
  font: 12px/20px 'Helvetica Neue', Arial, Helvetica, sans-serif;
  position: absolute;
  bottom: 0;
  z-index: 10;
  width: 400px;
  max-width: 100%;
  left: calc(50% - 200px);
}
.map-overlay .map-overlay-inner {
  padding: 10px;
  background-color: rgba(255,255,255,0.75);
  box-shadow: 0 1px 2px rgba(0,0,0,0.2);
  border-radius: 3px;
  margin-bottom: 20px;
}
.map-overlay .map-overlay-inner > fieldset {
  border: none;
}
.boxdraw {
  background: rgba(56,135,190,0.1);
  border: 2px solid #3887be;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
}
section.story {
  position: relative;
}
section.story:after {
  content: "";
  display: block;
  clear: both;
}
.storyline {
  background: #fefefe;
  color: #444;
}
.storyline {
  margin-left: calc(100% - 400px);
}
.storyline .chapter {
  padding: 50px 50px;
  min-height: 100vh;
  border-bottom: 1px solid #205356;
}
.step {
  position: relative;
}
.numbering {
  position: absolute;
  left: -40px;
  top: -2px;
  border: 1px solid #bbb;
  color: #bbb;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  line-height: 2;
  text-align: center;
  font-size: 12px;
  display: block;
}
.specieslist {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.specieslist li {
  flex: 1 0 auto;
  position: relative;
  display: block;
  min-width: 50%;
  margin: 2px;
}
.specieslist img {
  width: 64px;
  height: 64px;
  margin-right: 10px;
}
.specieslist p {
  padding-top: 10px;
  display: block;
}
.specieslist p:nth-of-type(2) {
  font-style: italic;
  color: #b5b5b5;
}
.media.isActive {
  color: #444;
  background: #eee;
}
.media {
  cursor: pointer;
  zoom: 1;
  color: #888;
}
.media>:first-child {
  float: left;
  margin-right: 10px;
}
.media img {
  max-width: 64px;
  background: #eee;
  border: none;
}
.media>* {
  display: inline-block;
}
.media p {
  margin: 0;
  font-size: 0.9em;
}
#speciesTypeahead > p {
  display: inline-block;
  width: calc(100% - 20px - 64px);
}
#speciesTypeahead > p >span,
#speciesTypeahead > p >span>span {
  display: inline-block;
  width: 100%;
}
.typeahead,
.typeahead:focus {
  outline: none;
  border: 1px solid #eaeaea;
  padding: 5px 10px;
  font-size: 0.9em;
  width: calc(100% - 20px);
}
.tt-menu {
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 1px 1px rgba(0,0,0,0.1);
  width: calc(100% - 20px);
}
.tt-menu .tt-suggestion {
  padding: 2px 5px;
  border-bottom: 1px solid #ddd;
}
.tt-menu .tt-suggestion.tt-cursor {
  background-color: #ddd;
}
.filterDescr >div {
  display: inline-block;
}
html,
body {
  margin: 0;
  padding: 0px;
  font-size: 14px;
  line-height: 1.5;
  font-family: 'Open Sans', sans-serif;
  background: #fefefe;
  color: #444;
  font-weight: 100;
}
* {
  box-sizing: border-box;
}
header {
  height: 70px;
  background: #81c388;
  color: #fff;
}
section.introduction {
  min-height: calc(100vh - 70px);
  background: #81c388;
}
section.introduction >div {
  margin: auto;
  max-width: 600px;
  padding: 50px;
}
h1,
h2,
h3 {
  font-size: 20px;
}
h2 {
  font-size: 22px;
}
.app .description {
  font-weight: bold;
}
section.description>div {
  width: 800px;
  max-width: 90%;
  margin: auto;
  padding: 20px;
}
.no-ui-slider {
  width: 100%;
}
.no-ui-slider .slider-labels {
  list-style: none;
  padding: 0;
  margin-top: 5px;
  margin-left: -3px;
  width: 100%;
  text-align: left;
  font-size: 13px;
}
.no-ui-slider .slider-labels li {
  color: #d3d3d3;
  display: inline;
  float: left;
  cursor: pointer;
}
.no-ui-slider .slider-labels li:nth-last-child(1) {
  position: absolute;
  margin-left: -3px;
  width: 25px;
}
.no-ui-slider .slider-labels li:hover {
  color: #333;
}
.no-ui-slider .slider-labels li.active {
  color: #333;
}
.noUi-target,
.noUi-target * {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-touch-action: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.noUi-base {
  width: 100%;
  height: 100%;
  position: relative;
}
.noUi-origin {
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
}
.noUi-handle {
  position: relative;
  z-index: 1;
  cursor: move;
}
.noUi-stacking .noUi-handle {
/* This class is applied to the lower origin when
	   its values is > 50%. */
  z-index: 10;
}
.noUi-stacking + .noUi-origin {
/* Fix stacking order in IE7, which incorrectly
   creates a new context for the origins. */
  *z-index: -1;
}
.noUi-state-tap .noUi-origin {
  -webkit-transition: left 0.3s, top 0.3s;
  transition: left 0.3s, top 0.3s;
}
.noUi-state-drag * {
  cursor: inherit !important;
}
.noUi-horizontal {
  height: 6px;
}
.noUi-horizontal .noUi-handle {
  width: 14px;
  height: 14px;
  top: -5px;
  left: -7px;
}
.anoUi-horizontal .noUi-handle-lower {
  left: -1px;
}
.anoUi-horizontal .noUi-handle-upper {
  left: -13px;
}
.noUi-horizontal.noUi-extended {
  padding: 0 15px;
}
.noUi-horizontal.noUi-extended .noUi-origin {
  right: -15px;
}
.noUi-vertical {
  width: 18px;
}
.noUi-vertical .noUi-handle {
  width: 20px;
  height: 20px;
  left: -10px;
  top: -10px;
  border-radius: 100%;
}
.noUi-vertical.noUi-extended {
  padding: 15px 0;
}
.noUi-vertical.noUi-extended .noUi-origin {
  bottom: -15px;
}
.noUi-background {
  background: #fafafa;
}
.noUi-connect {
  background: #142736;
  -webkit-transition: background 450ms;
  transition: background 450ms;
}
.noUi-origin {
  border-radius: 10px;
}
.noUi-target {
  border-radius: 10px;
  border: 1px solid #d3d3d3;
}
.noUi-dragable {
  cursor: w-resize;
}
.noUi-vertical .noUi-dragable {
  cursor: n-resize;
}
.noUi-handle {
  border: 1px solid #8b8b8b;
  border-radius: 100%;
  background: #eee;
  cursor: default;
}
.noUi-handle:before,
.noUi-handle:after {
  display: block;
  position: absolute;
  height: 14px;
  width: 1px;
  background: #e8e7e6;
  left: 14px;
  top: 6px;
}
.noUi-handle:after {
  left: 17px;
}
.noUi-vertical .noUi-handle:before,
.noUi-vertical .noUi-handle:after {
  width: 14px;
  height: 1px;
  left: 6px;
  top: 14px;
}
.noUi-vertical .noUi-handle:after {
  top: 17px;
}
[disabled].noUi-connect,
[disabled] .noUi-connect {
  background: #b8b8b8;
}
[disabled] .noUi-handle {
  cursor: not-allowed;
}
.ct-series-b .ct-line {
  stroke: #555;
}
.ct-line {
  stroke-width: 2px;
}
.ct-series-a .ct-point {
  stroke: #c38789;
}
.ct-point {
  stroke-width: 5px;
  stroke-linecap: round;
  transition: all 0.2s linear;
}
.ct-point:hover {
  stroke-width: 10px;
  stroke: #ff6347;
}
.chartist-tooltip {
  position: absolute;
  background: #888;
  color: #eee;
  padding: 10px;
}
#stats {
  display: none;
  font-size: 12px;
}
#stats >p {
  margin-top: 0;
}
